    transition: all 0.3s ease-in-out;
    margin: 0;
    text-align: start;
    color: white;
    text-align: center;
    display: flex;
    align-items: center;

}

.selected {
    font-size: 70px !important;
}

.not {
    opacity: 0.5 ;
}